export default function recipesCache(decorated) {
  return (args) => {
    const cacheKey = `recipesCache.${args.recipes.length}.${decorated.name}`;
    const cached = localStorage.getItem(cacheKey);
    if (cached != null) {
      return JSON.parse(cached);
    }

    const value = decorated(args);
    localStorage.setItem(cacheKey, JSON.stringify(value));
    return value;
  };
}



